* {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  /* font-family: proxima-nova, sans-serif; */
}

body {
  background: aliceblue
  /* background-image: url(https://8b.com/assets/images/securebg.png); */
}

.footerBg{
  background: url(https://i.pinimg.com/originals/2f/69/a2/2f69a2f7b472d4539a0e7a1fdd22bc00.jpg);
}

.top-border-green{
  border-top: 10px solid lightseagreen;
}

@media (min-width: 600px){
.MuiTab-root {
    min-width: 120px !important;
  }
}

.MuiSvgIcon-colorSecondary{
  color : #9e0036 !important
}
.MuiChip-label{
  line-height: 12px;
}

.icon_new{
  font-size: 80px;
  padding: 10px;
}

.mt-10 {
  margin-top: 60px;
}

.contactUsList a{
  color: lightseagreen !important
}

h1,h2,h3,h4,h5,h6,p,label{
  /* color: #45460c; */
  color: #183153;
}

a.link{
  color: #ff7f00 !important;
}

@keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
  }
}

.rotate {
  animation: rotation 2s infinite linear;
  -webkit-animation: rotation 2s infinite linear;
}

.top-carousel * {
  color: #efefef !important
}
.carousel-control-prev div,
.carousel-control-next div{
   background: #000;
    border-radius: 50%;
    height: 50px;
    width: 50px;
    line-height: 60px;
    opacity: .6;
}

.carousel-indicators li{
  display: none
}
.top-carousel .carousel-indicators li{
  display: block;
  height: 30px !important;
  width: 10px !important;
  background: grey !important;
  border: none !important;
  border-radius: 0 !important;
}
.top-carousel .carousel-indicators li.active{
  background: #183153 !important;
}

.parallax {
  min-height: calc(80vh);
  background-attachment: fixed;
  background-position: center left;
  background-repeat: no-repeat;
  background-size: 100% 100% !important;
}

@media screen and (max-width: 960px) {
  .parallax {
    background-size: 100% 100%;
  }
}

.parallax_physio {
  min-height: calc(50vh);
  background-image: linear-gradient(rgba(0,0,0, 0.8), rgba(0,0,0, 0.8)), url(https://thumbs.dreamstime.com/b/highlighted-spine-man-physiotherapy-digital-composite-men-53051926.jpg);
}

.parallax_sigup {
  background-image: linear-gradient(rgba(0,0,0, 0.8), rgba(0,0,0, 0.8)), url(https://www.portea.com/static/dd69aac5f271983fc375fe1c227f752e/d8826/1366x522-7.jpg);
  min-height: calc(30vh);
}

.parallax_contact {
  background-image: linear-gradient(rgba(0,0,0, 0.6), rgba(0,0,0, 0.6)), url("https://www.smg-marble.com/wp-content/uploads/2016/10/contactus-banner.jpg");
  min-height: calc(60vh) !important;
}

.parallax_testimonials {
  min-height: calc(40vh);
  background-image: linear-gradient(rgba(255,255,255, 0.8), rgba(255,255,255, 0.8)), url(https://circusppc.com/wp-content/uploads/2019/06/AdobeStock_220758371-2890x960.jpeg);
}

button:focus {
  outline: none !important;
}

.customBtn .MuiButton-label{
  color: #fff
}

.customBtnText button{
  padding: 0;
  min-width: auto;
}
.customBtnText .MuiButton-label{
  color: lightseagreen
}

.customBtnDisabled .MuiButton-root.Mui-disabled{
  color: #fff !important;
  opacity: .6
}

/* material verwrite css  */
.MuiStepIcon-root.MuiStepIcon-completed{
  color: #ff7f00 !important;
}

.rotateBtn {
  transform: rotate(180deg);
}

.pointer {
  cursor: pointer;
}

.w-10{
  width: 10% !important
}

/* admin page  */
.admin_container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.admin_menu{
  display: flex;
  flex-direction: column;
  width: 8%;
}
.admin_content{
  width: 85%;
  margin: 10px auto;
}
.admin_image{
  width: 50%
}
/* admin page end  */

@media screen and (min-width: 320px) and (max-width: 960px) {
  .parallax{
    background-size: cover !important;
    min-height: calc(60vh) !important;
  }
  .parallax .row{
    width: 100% !important
  }
  .admin_container{
    flex-direction: column;
  }
  .admin_menu{
    flex-direction: row !important;
    width: 100% !important;
    margin-bottom: 10px;
  }
  .admin_content{
    width: 100%;
    margin: 0 auto;
  }
  .admin_image{
    width: 75%
  }
}

@-webkit-keyframes zoom {
  from {
    -webkit-transform: scale(1, 1);
  }
  to {
    -webkit-transform: scale(1.2, 1.2);
  }
}

@keyframes zoom {
  from {
    transform: scale(1, 1);
  }
  to {
    transform: scale(1.2, 1.2);
  }
}

.parallax-img {
  -webkit-animation: zoom 10s;
  animation: zoom 10s infinite;
}